import Bar from "./components/Bar";
import Header from "./components/Header";
import tw from "tailwind-styled-components"
import Img from "./programImg/flutter.svg"
import logo from "./programImg/TAB_logo_textWhite.png";
import activity from "./programImg/flutterActivity.png"
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import "swiper/css/pagination";
import "swiper/css/autoplay";
import { Navigation } from "swiper";

const Item = tw.div`
    flex
    flex-col
    space-y-3
`
const ItemTitle = tw.span`
    text-xl
    font-bold
`
const ItemDetail = tw.span`
    ml-5
`
export default function ProgramDetail() {
    return(
        <div>
            <div className='bg-[#865DFF] flex flex-col items-center justify-center py-3'>
                <img src={logo} className='h-52'/>
                <div className='bg-[#E384FF] rounded-3xl p-2 px-3'>
                    <span className='text-white font-extrabold  '>
                        Recruitment of new members for the 41st TAB
                    </span>
                </div>
            </div>

            <div className="mx-auto  w-full lg:w-[1024px] p-7 ">
                <span className="text-3xl font-extrabold">flutter session - flutter  </span>
                <img src ={Img} className="rounded-3xl w-full my-5"/>
                <div className="bg-[#F3F3F3] p-5 rounded-3xl mb-5">     flutter!     ? </div>
                <div className=" space-y-10">
                    <Item className="flex flex-col">
                        <ItemTitle> </ItemTitle>
                        <ItemDetail >Flutter  ,  </ItemDetail>
                    </Item>
                    <Item>
                        <ItemTitle> </ItemTitle>
                        <ItemDetail>2022.05.10 ~ </ItemDetail>
                    </Item>
                    <Item>
                        <ItemTitle> </ItemTitle>
                        <div className="ml-5">
                              google meet   .<br/>
                               .
                            <ul className="list-decimal py-7 ml-6">
                                <li>
                                    Flutter UI
                                </li>
                                <li>
                                    Flutter       
                                </li>
                                <li>
                                    StatelessWidget
                                </li>
                                <li>
                                    StatefulWidget , State
                                </li>
                                <li>
                                    
                                </li>
                            </ul>
                                   !<br/>
                              Discord     .
                        </div>
                    </Item>
                    <Item>
                        <ItemTitle> </ItemTitle>
                        <Swiper
                            className="mt-5 w-full h-96 bg-[#F3F3F3] rounded-3xl"
                            modules={[Navigation]}
                            spaceBetween={50}
                            slidesPerView={1}
                            navigation
                            onSwiper={(swiper) => console.log(swiper)}
                        >
                            <SwiperSlide className="" >
                                <img src = {activity} className="h-96 object-contain"/>
                            </SwiperSlide>
                            <SwiperSlide className="">
                                <img src = {activity} className="h-96 object-contain"/>
                            </SwiperSlide>
                            <SwiperSlide className="">
                                <img src = {activity} className="h-96 object-contain"/>
                            </SwiperSlide>
                        </Swiper>
                    </Item>
                </div>
            </div>
            <Bar/>
        </div>
    )
}